import { Actionsheet, Button } from 'mint-ui';
// 
export default {
  data() {
    return {
      sheetVisible: false,
      actions: []
    };
  },
  components: {
    'actionsheet' : Actionsheet ,
    'xButton' : Button

  },
  methods: {
    takePhoto() {
      console.log('taking photo');
    },
    openAlbum() {
      console.log('opening album');
    },
    goBack() {
      history.go(-1);
    }
  },
  mounted() {
    this.actions = [{
      name: '',
      method: this.takePhoto
    }, {
      name: '',
      method: this.openAlbum
    }];
  }
};






